/**
 * useAuth Hook - Manages authentication state and operations
 * Handles signup, login, logout, and current user context
 * 
 * FIXES:
 * - NO auto-login without token verification
 * - localStorage only restored if it exists and is valid
 * - logout clears both localStorage and cookie
 * - isInitialized flag ensures app waits for auth check
 */

"use client";

import { useState, useCallback, useEffect } from "react";
import { authApi, AuthUser } from "@/lib/api";

interface UseAuthState {
  user: AuthUser | null;
  token: string | null;
  isLoading: boolean;
  error: string | null;
  isAuthenticated: boolean;
}

const initialState: UseAuthState = {
  user: null,
  token: null,
  isLoading: false,
  error: null,
  isAuthenticated: false,
};

/**
 * Custom hook for authentication
 * Manages user state and provides auth methods
 */
export function useAuth() {
  const [state, setState] = useState<UseAuthState>(initialState);
  const [isInitialized, setIsInitialized] = useState(false);

  // Initialize from localStorage (only if explicitly saved)
  useEffect(() => {
    try {
      const stored = localStorage.getItem("auth-state");
      if (stored) {
        const parsed = JSON.parse(stored);
        // Only restore if both user and token exist
        if (parsed?.user && parsed?.token) {
          setState(parsed);
        } else {
          // Invalid state, clear localStorage
          localStorage.removeItem("auth-state");
        }
      }
    } catch (error) {
      console.error("Failed to restore auth state:", error);
      localStorage.removeItem("auth-state");
    }
    // Mark as initialized so components know auth check is complete
    setIsInitialized(true);
  }, []);

  // Persist state to localStorage (only if authenticated)
  useEffect(() => {
    if (isInitialized) {
      if (state.isAuthenticated && state.token && state.user) {
        localStorage.setItem("auth-state", JSON.stringify(state));
      } else {
        // If not authenticated, clear localStorage
        localStorage.removeItem("auth-state");
      }
    }
  }, [state, isInitialized]);

  const handleSignup = useCallback(
    async (data: {
      email: string;
      password: string;
      confirmPassword: string;
      fullName: string;
    }) => {
      setState((prev) => ({ ...prev, isLoading: true, error: null }));
      try {
        const response = await authApi.signup(data);
        setState({
          user: response.user,
          token: response.token,
          isAuthenticated: true,
          isLoading: false,
          error: null,
        });
        return response;
      } catch (error: any) {
        const errorMessage = error.message || "Signup failed";
        setState((prev: any) => ({
          ...prev,
          error: errorMessage,
          isLoading: false,
        }));
        throw error;
      }
    },
    []
  );

  const handleLogin = useCallback(
    async (data: { email: string; password: string; rememberMe?: boolean }) => {
      setState((prev) => ({ ...prev, isLoading: true, error: null }));
      try {
        const response = await authApi.login(data);
        setState({
          user: response.user,
          token: response.token,
          isAuthenticated: true,
          isLoading: false,
          error: null,
        });
        return response;
      } catch (error: any) {
        const errorMessage = error.message || "Login failed";
        setState((prev: any) => ({
          ...prev,
          error: errorMessage,
          isLoading: false,
        }));
        throw error;
      }
    },
    []
  );

  const handleLogout = useCallback(async () => {
    setState((prev) => ({ ...prev, isLoading: true }));
    try {
      await authApi.logout();
      // Clear all auth state
      setState(initialState);
      // Clear localStorage
      localStorage.removeItem("auth-state");
    } catch (error: any) {
      setState((prev: any) => ({
        ...prev,
        error: error.message || "Logout failed",
        isLoading: false,
      }));
      throw error;
    }
  }, []);

  const setError = useCallback((error: string | null) => {
    setState((prev) => ({ ...prev, error }));
  }, []);

  const clearError = useCallback(() => {
    setState((prev) => ({ ...prev, error: null }));
  }, []);

  return {
    ...state,
    isInitialized,
    signup: handleSignup,
    login: handleLogin,
    logout: handleLogout,
    setError,
    clearError,
  };
}
